___CSS_LOADER_EXPORT___.push([module.id, `/*
Josh's Custom CSS Reset
https://www.joshwcomeau.com/css/custom-css-reset/
*/
*, *::before, *::after {
    box-sizing: border-box;
}
* {
    margin: 0;
}
body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}
img, picture, video, canvas, svg {
    display: inline-block;
    max-width: 100%;
}
input, button, textarea, select {
    font: inherit;
}
p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
    hyphens: auto;
}
p {
    text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
    text-wrap: balance;
}
button {
    line-height: 1;
}
button:hover {
    cursor: pointer;
}

body {
    height: 100vh;
}

h1 {
    text-align: center;
    font-size: 3rem;
}

#game-message {
    text-align: center;
    font-size: 2rem;
}

.grid-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.grid {
    height: 402px;
    width: 402px;
    border: 1px solid black;
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 1fr);
}

.grid-square {
    border: 1px solid black;
}

.direction {
    text-align: center;
}

.clicked {
    background-color: green;
}

.hovered {
    background-color: blue;
}

#battle-page {
    display: none; /* Enabled after ship placement is done */
    flex-wrap: wrap;
    justify-content: center;
    gap: 32px;
}

.computer.grid-square:hover {
    filter: brightness(75%);
}

.computer.grid.disabled {
    pointer-events: none;
    opacity: 0.5;
}

#win-page {
    display: none;
    flex-direction: column;
    align-items: center;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;;;CAGC;AACD;IACI,sBAAsB;AAC1B;AACA;IACI,SAAS;AACb;AACA;IACI,gBAAgB;IAChB,mCAAmC;AACvC;AACA;IACI,qBAAqB;IACrB,eAAe;AACnB;AACA;IACI,aAAa;AACjB;AACA;IACI,yBAAyB;IACzB,aAAa;AACjB;AACA;IACI,iBAAiB;AACrB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,cAAc;AAClB;AACA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,uBAAuB;IACvB,aAAa;IACb,sCAAsC;IACtC,mCAAmC;AACvC;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,aAAa,EAAE,yCAAyC;IACxD,eAAe;IACf,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,oBAAoB;IACpB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB","sourcesContent":["/*\nJosh's Custom CSS Reset\nhttps://www.joshwcomeau.com/css/custom-css-reset/\n*/\n*, *::before, *::after {\n    box-sizing: border-box;\n}\n* {\n    margin: 0;\n}\nbody {\n    line-height: 1.5;\n    -webkit-font-smoothing: antialiased;\n}\nimg, picture, video, canvas, svg {\n    display: inline-block;\n    max-width: 100%;\n}\ninput, button, textarea, select {\n    font: inherit;\n}\np, h1, h2, h3, h4, h5, h6 {\n    overflow-wrap: break-word;\n    hyphens: auto;\n}\np {\n    text-wrap: pretty;\n}\nh1, h2, h3, h4, h5, h6 {\n    text-wrap: balance;\n}\nbutton {\n    line-height: 1;\n}\nbutton:hover {\n    cursor: pointer;\n}\n\nbody {\n    height: 100vh;\n}\n\nh1 {\n    text-align: center;\n    font-size: 3rem;\n}\n\n#game-message {\n    text-align: center;\n    font-size: 2rem;\n}\n\n.grid-wrapper {\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n}\n\n.grid {\n    height: 402px;\n    width: 402px;\n    border: 1px solid black;\n    display: grid;\n    grid-template-columns: repeat(10, 1fr);\n    grid-template-rows: repeat(10, 1fr);\n}\n\n.grid-square {\n    border: 1px solid black;\n}\n\n.direct